<template lang="html">
  <div class="coin-manage-page">
    <!-- 头部 -->
    <default-head header="召币管理"></default-head>

    <div class="coin-box">
      <count-item class="borderB" @click.native="$router.push('/coindetail')" textLeft="我的召币" textRight="查看明细" :isShow="true">
        <span class="middle wrongColor">{{this.userInfo.zhaoBiCount}}币</span>
      </count-item>

      <coin-list v-if='false'></coin-list>

      <light-title text="获取召币" color="#333"></light-title>
      <count-item class="borderB" textLeft="服务评价" textRight="评价后随即赠送10-100币" :isShow="false"></count-item>
      <!-- <count-item class="borderB" textLeft="通话态度好评" textRight="赠送比例1：3" :isShow="true"></count-item>
      <count-item class="borderB" textLeft="产品售后好评" textRight="赠送比例1：5" :isShow="true"></count-item> -->
      <count-item class="borderB" textLeft="分享推荐安装" textRight="赠送20币" :isShow="false"></count-item>
      <count-item class="borderB" textLeft="广告费消费" textRight="¥ 1=100币" :isShow="false"></count-item>
      <count-item class="borderB" textLeft="举报诈骗商家" textRight="返超值召币" :isShow="false"></count-item>

      <light-title text="扣除召币" color="#333"></light-title>
      <count-item class="borderB" textLeft="服务评价差评" textRight="一星扣30币、二星扣20币" :isShow="false"></count-item>
      <!-- <count-item class="borderB" textLeft="产品售后差评" textRight="一星扣50币、二星扣40币" :isShow="true"></count-item> -->
      <count-item class="borderB" textLeft="信息不符被举报" textRight="扣100币" :isShow="false"></count-item>
      <count-item class="borderB" textLeft="商家违规操作" textRight="1次扣100币" :isShow="false"></count-item>
    </div>
  </div>
</template>

<script>
import defaultHead from '@/components/head/defaultHead'
import countItem from '@/components/personcenter/countItem'
import lightTitle from '@/components/common/lightTitle'
import coinList from '@/components/coinManage/coinList'

export default {
  data () {
    return {
    }
  },
  computed: {
    userInfo: function () {
      return JSON.parse(sessionStorage.getItem('userInfo'))
    }
  },
  components: { defaultHead, countItem, lightTitle, coinList }
}
</script>

<style lang="less" scoped>
@import '../assets/css/variable.less';

.coin-manage-page {
  padding-top: .88rem;
  .coin-box {
    .middle {
      font-size: .36rem;
    }
  }
}

</style>



